.summary-container {
  margin-top: 100px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  background-color: #fefefe;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.summary-container h2 {
  margin-bottom: 20px;
}

.summary-container p {
  font-size: 1.1rem;
  margin: 10px 0;
}

.message {
  font-size: 1.3rem;
  margin-top: 20px;
  font-weight: bold;
  color: #333;
}

.summary-container button {
  margin-top: 25px;
  padding: 12px 25px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  background-color: #61dafb;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.summary-container button:hover {
  background-color: #21a1f1;
}
