.about-container {
  margin-top: 100px;
  padding: 20px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fdfdfd;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0,0,0,0.1);
  line-height: 1.6;
  font-family: Arial, sans-serif;
}

.about-container h1 {
  text-align: center;
  margin-bottom: 20px;
}

.about-container section {
  margin-bottom: 30px;
}

.about-container ul {
  padding-left: 20px;
}

.about-container img {
  max-width: 100%;
  margin: 15px 0;
  border-radius: 8px;
  box-shadow: 0 0 6px rgba(0,0,0,0.1);
}

footer {
  text-align: center;
  margin-top: 40px;
  font-weight: bold;
  color: #333;
}
