.leaderboard-container {
  margin-top: 100px;
  padding: 20px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  background: #fefefe;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
  text-align: center;
}

.leaderboard-container h2 {
  margin-bottom: 20px;
}

.sort-buttons {
  margin-bottom: 15px;
}

.sort-buttons button {
  margin: 0 10px;
  padding: 8px 18px;
  border: none;
  border-radius: 6px;
  background-color: #61dafb;
  cursor: pointer;
  font-weight: bold;
}

.sort-buttons button:hover {
  background-color: #21a1f1;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  padding: 12px;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f1f1f1;
  font-weight: bold;
}




tbody tr:hover {
  background-color: #f1f9ff;
  transform: scale(1.01);
  transition: 0.2s;
}
