.app-footer {
  margin-top: 40px;
  padding: 20px;
  text-align: center;
  background-color: #f1f1f1;
  color: #333;
  font-size: 14px;
  box-shadow: 0 -2px 5px rgba(0,0,0,0.05);
}

.social-links {
  margin-top: 10px;
}

.social-links a {
  margin: 0 10px;
  color: #0077b5;
  font-weight: bold;
  transition: color 0.3s;
}

.social-links a:hover {
  color: #005582;
}
