.quiz-container {
  margin-top: 100px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
}

.progress-bar {
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.question-box h3 {
  margin-bottom: 15px;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.options button {
  padding: 12px;
  font-size: 1rem;
  border-radius: 8px;
  border: 2px solid #ccc;
  cursor: pointer;
  transition: 0.3s;
}

.options button:hover:enabled {
  background-color: #eee;
}

.options .correct {
  background-color: #d4edda;
  border-color: #28a745;
}

.options .wrong {
  background-color: #f8d7da;
  border-color: #dc3545;
}

.controls {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.controls button {
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 6px;
  border: none;
  background-color: #61dafb;
  cursor: pointer;
}

.timer {
  text-align: center;
  margin-top: 15px;
  font-weight: bold;
}



.question-box {
  animation: slideIn 0.5s ease;
}

@keyframes slideIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.options button {
  transition: background-color 0.3s, transform 0.3s;
}

.options button:hover:enabled {
  transform: scale(1.03);
}
